<template>
  <div id="app">
    <header class="header">
      <img
        class="header__logo"
        src="./assets/logo.svg"
        alt="logo that displays white plane on blue circle"
      />
    </header>
    <Canvas />
    <main class="content">
      <FilterCard 
        v-bind:filter="filterState"
        v-on:currency-changed="handleCurrencyChange($event)"
        v-on:only="handleOnlyStop($event)"
      />

      <TicketsList
        v-bind:currencyRates="rates"
        v-bind:cur="filterState.currency"
        v-bind:stops="filterState.stops"
      />
    </main>
  </div>
</template>

<script>
import FilterCard from './components/FilterCard.vue';
import TicketsList from './components/TicketsList.vue';
import Canvas from './components/Canvas.vue';
import axios from 'axios';

export default {
  name: 'App',

  components: {
    FilterCard,
    TicketsList,
    Canvas,
  },

  data() {
    return {
      rates: {
        RUB: { sym: '₽', val: 1 },
        USD: { sym: '$', val: 70.73 },
        EUR: { sym: '€', val: 79.89 },
      },

      filterState: {
        currency: 'RUB',

        stops: {
          0: true,
          1: true,
          2: true,
          3: true,
          all: true,
        },
      },
    };
  },

  methods: {
    handleCurrencyChange(currency) {
      this.filterState.currency = currency;
    },

    handleOnlyStop(val) {
      const stops = this.filterState.stops;

      for (const key in stops) {
        stops[key] = false;
      }

      stops[val] = true;
      this.filterState.stops = stops;
    },
  },

  created() {
    axios.get('https://api.exchangeratesapi.io/latest?symbols=USD,RUB')
      .then(response => {
        const rates = response.data.rates;
        this.rates.EUR.val = rates.RUB;
        this.rates.USD.val = rates.RUB / rates.USD;
        console.log('rates from exchangeratesapi.io:');
        console.log('USD: ', this.rates.USD.val, 'EUR: ', this.rates.EUR.val);
      })
      .catch(error => console.log(error));
  },
}
</script>

